import React from "react";
import { BrowserRouter as Router, Route, Routes } from "react-router-dom";
import Home from "./pages/Home";
import Me from "./pages/Me";
import Experience from "./pages/Experience";
import ExperienceM from "./pages/ExperienceM";
import ScrollToTop from "./components/ScrollToTop";
import Projects from "./pages/Projects";
import ProjectsM from "./pages/ProjectsM";
import {isMobile} from "react-device-detect";
import Navbar from "./components/Navigation/Navbar";
import NavItems from "./components/Navigation/NavItems";


export default function App() {
    const appCss = [
        'flex',
        'flex-col',
        'h-full',
        'w-screen',
        'overflow-hidden',
        isMobile ? '' : ''
    ].join(' ');

    function checkDevice(){
        if(!isMobile){
            return (
                <>
                    <ScrollToTop/>
                    <Home/>
                    <Me/>
                    <Experience/>
                    <Projects/>
                </>
            )
        } else {
            return (
                <>
                <Routes>
                    <Route path='johnnytnguyen/' element={<Home/>}/>
                    <Route path='johnnytnguyen/me' element={<Me/>}/>
                    <Route path='johnnytnguyen/experience' element={<ExperienceM/>}/>
                    <Route path='johnnytnguyen/projects' element={<ProjectsM/>}/>
                </Routes>
                </>
            )
        }
    }

    return (
        <main className={appCss + ' App'}>
            <Router>
                {isMobile ? <NavItems/> : ''}
                {checkDevice()}
            </Router>
        </main>
    );
}
