import React, {useState} from "react";
import { NavmainData } from "./Navmain-data";
import { isMobile } from "react-device-detect";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faBars, faTimes } from "@fortawesome/free-solid-svg-icons"
import NavItems from "./NavItems"

const navMainItemClass = [
    'nav-container',
    'flex',
    'h-9',
    'justify-around',
    isMobile ? '' : 'bg-black',
    'bg-opacity-50',
    'text-white'
].join(' ');

export default function NavMain(){
    const [isClicked, setIsClicked ] = useState(false);

    const data = isMobile ? NavmainData.slice(0).reverse() : NavmainData;
    return (
        <nav id='nav-main-items' className={navMainItemClass}>
            <div className={isMobile ? "w-full flex flex-col" : "hidden"} onClick={() => setIsClicked(!isClicked)}>
                <FontAwesomeIcon className={'m-2 text-3xl'} icon={isClicked ? faTimes : faBars}/>
                <div className={isClicked ? 'flex flex-col ' : 'hidden'}>
                    <NavItems/>
                </div>
            </div>
            <div className={isMobile ? 'hidden' : 'w-full flex justify-around'}>
                <NavItems/>
            </div>
        </nav>
    );
}
